.app-header {
   height: 560px;
   color: #FFF;
   display: flex;
   flex-direction: column;
   align-items: center;
   width: 100%;
   padding: 10px;
   z-index: -1;
   font-family: Air-Americana;

   // background-image: url(http://www.bookingisrael.com/UploadedImages/AtractionFolder/5/Image_17750.jpg);
   // background-image: url(http://www.bookingisrael.com/UploadedImages/AtractionFolder/5/Image_17749.jpg);
   background-image: url('../imgs/header.jpeg');
   background-repeat: no-repeat;
   /* background-attachment: fixed!important; */
   background-position: 50%!important;
   background-size: cover!important;

   h1 {
      font-size: rem(65px);
      margin-bottom: 50px;
      margin-top: 4px;
      text-align: center;
      text-shadow: 0px 0px 5px black;
      // font-family: Skyfont;
      font-family: Air-Americana;
   }

   nav {
      display: flex;
      gap: 25px;
      cursor: pointer;
      font-size: rem(27px);

      .active {
         text-decoration: underline;
         color: gold;
      }

      a {
         color: #FFF;
         // color: #121212;
         text-shadow: 0px 0px 5px black;
         text-decoration: none;

         &:hover {
            color: #121212;
            text-shadow: 0px 0px 2px white;
         }

      }
   }
}